*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #000;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

body {
  min-height: 100%;
  -webkit-user-select: none;
  user-select: none;
  will-change: opacity;
  flex-flow: column;
  justify-content: stretch;
  align-items: flex-start;
  margin: 0;
  font-size: 1em;
  animation: .5s ease-out .7s backwards fadeIn;
  display: flex;
}

video, canvas, img {
  max-width: 100%;
  max-height: 100%;
}

a {
  color: inherit;
  cursor: pointer;
  font-style: italic;
  font-weight: 900;
  text-decoration: none;
}

.view {
  cursor: pointer;
  touch-action: none;
  tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  background-color: #000;
  position: relative;
}

.view.hide {
  display: none;
}

.guide {
  z-index: 1;
  max-height: 50%;
  color: #fff;
  text-align: center;
  opacity: .5;
  will-change: opacity;
  background-color: #333;
  flex-flow: wrap;
  place-content: stretch center;
  align-items: stretch;
  font-family: sans-serif;
  transition: opacity .8s ease-out;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

@media (any-hover: none) {
  .guide {
    opacity: 1;
  }
}

.guide:hover, .guide:focus, .guide:focus-within {
  opacity: 1;
  transition: opacity .2s ease-in;
}

.info {
  flex-flow: wrap;
  flex: 1 0 100%;
  justify-content: center;
  align-items: flex-start;
  margin: 0;
  padding: .5em;
  display: flex;
}

.error {
  color: #fff;
  background-color: #ff3c00;
  font-weight: 900;
}

.error.hide {
  display: none;
}

.link {
  color: inherit;
  text-align: center;
  cursor: pointer;
  will-change: color, background-color;
  background: #0003;
  border-radius: 3px;
  flex: none;
  margin: .2em;
  padding: .3em .6em;
  font-size: 1em;
  font-weight: 900;
  line-height: 1.3;
  text-decoration: none;
  transition: color .2s ease-out, background-color .2s ease-out;
}

.link:hover, .link:focus {
  color: #000e;
  background-color: #fffa;
}

.link:active, .link:target {
  color: #000;
  background-color: #fff;
}

.flip-guide {
  width: 100%;
  text-align: center;
  cursor: row-resize;
  color: #333;
  outline-offset: -3px;
  z-index: 1;
  background-color: #fff;
  border: 1px solid #333;
  border-width: 1px 0;
  flex: 1 0 100%;
  padding: .4em;
  font-family: monospace;
  font-size: 1.2em;
  font-weight: 900;
  display: block;
  position: sticky;
  top: 0;
}

.flip-guide:before, .flip-guide:after {
  content: "⇳";
  margin: 0 1em;
  display: inline-block;
  transform: scale(1.7);
}

#flip-guide {
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

#flip-guide:focus + .flip-guide {
  outline: 2px solid #33f;
}

#flip-guide:not(:checked) ~ .info, #flip-guide:not(:checked) ~ .link {
  display: none;
}

.fallback {
  width: 100%;
  height: 720px;
  margin: auto;
  position: relative;
}

